*,
*:before,
*:after {
  box-sizing: border-box;
}

body {
  margin: 0;

  font-family: Arial, serif;
}

.App {
  display: block;

  width: 100%;
}

.container {
  width: 1200px;

  margin: 0 auto;
}

.jokes-wrapper {
  position: absolute;

  height: 550px;
  width: 1050px;

  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);

  background-color: #eee;
  border-radius: 10px;

  text-align: center;

  box-shadow: #333 5px 5px 15px 5px;
}

.jokes-container {
  width: 100%;
  height: 95%;
  max-height: 500px;

  display: flex;
  flex-direction: column;
  overflow: scroll;
  overflow-x: hidden;
  overflow-scrolling: auto;

  text-align: center;
}

ul {
  padding: 10px;
}

li {
  list-style-type: none;
}

.joke {
  margin-top: 10px;

  padding: 15px;

  text-align: left;

  border: 5px solid black;

  background-color: #FF1300;

  color: #fff;
}

.actions {
  margin-top: 5px;

  display: flex;
  flex-direction: row;

  justify-content: space-around;
}

.actions label {
  padding: 10px;
}

.date-picker {
  display: flex;
  flex-direction: row;

  height: 35px;
}

.jokes-requester {
  width: 200px;
  height: 35px;

  background-color: #4B2A82;

  border-radius: 5px;
  cursor: pointer;

  color: #ffffff;

  transition: background-color .1s linear;
}

.jokes-requester:hover {
  background-color: #7C45D6;
}

.blink {
  animation: blinker 1s linear infinite;
}

@keyframes blinker {
  25% {
    opacity: 0.7;
    background-color: #0FFBA2;
    color: #333333;
  }
  50% {
    opacity: 1;
    background-color: #FF9C0F;
    color: #333333;
  }
  75% {
    opacity: 0.7;
    background-color: #FE0F4F;
    color: #333333;
  }
}